import React from 'react'

const LogoBox = () => {
    return (
        <div>
            <div className="LogoBox" data-aos="fade-up" data-aos-delay="0" data-aos-duration="900">
                <span className="LogoText"> </span>
                <div className="NameBox">
                    <div className="Number">
                        3
                    </div>
                    <p> Miao Li-Han</p>
                </div>
            </div>
        </div>
    )
}

export default LogoBox